<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AddTicket') }}</span>
                    <span class="float-right">
                        <RouterLink to="/Ticketing/Index" class="p-action-btn text-white float-right">
                            <em class="fa fa-angle-double-left"></em>
                            <br />
                            {{ $t('BacktoList') }}
                        </RouterLink>
                    </span>
                </div>
            </div>
        </div>
        <div class="border p-3" v-if="isSuccessMessage">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12  ticket-thankyou text-center">
                <span class="checksign text-success"><i class="fa fa-check"></i></span>
                <h1 class="mt-4 mb-4">{{ $t('ThankYou') }}</h1>

                <div class="pb-4 ticket-number">
                    <P>{{ saveTicketStatus }}</P>
                    <div
                        style="font-size: 20px;position: relative;background: #fff3cd;padding: 35px 30px; margin: 20px; border: 4px dotted #856404;display: inline-block;color: #856404;">
                        {{ TicketNumber }}</div>
                </div>
            </div>
        </div>
        <Form ref="AddTicket">
            <dynamic-form ref="AddTicketForm" :buttons="buttons" :schema="FormSchema" v-if="!isSuccessMessage">
                <template v-slot:tgslot-Description="{ data }">

                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"
                        :class="{ 'is-invalid': data.error != '' }"></ckeditor>
                    <span class="invalid-feedback">{{ data.error }}</span>
                </template>
                <template v-slot:tgslot-addresFields="{ data }">
                    <div class="row">
                        <div class="col-md-6">
                            <dynamic-form ref="AddTicketForm1" :schema="StaticObj"></dynamic-form>
                        </div>
                        <div class="col-md-6">
                            <ManageMap class="row" mapType="MapBox" mapContainer="WorkOrder" :config="config"
                                mapHeight="355px" v-if="isShowMap" @GetAddress="GetAddress"></ManageMap>
                        </div>
                    </div>
                </template>
            </dynamic-form>
        </Form>
        <ManageCategory v-if="isManageCategory" modulename="Ticketing" submodulecode="Ticketing"
            v-bind:callbackfunction="GetDropDownData"></ManageCategory>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import ManageMap from './../../components/Common/MapComponent.vue';
import ManageCategory from './DetailsComponent/AddManageCategoryModal.vue';
export default {
    components: {
        ManageMap,
        ManageCategory
    },
    data: function () {
        return {
            config: {
                isShowDrawButton: true,
                isShowSearchBox: true
            },
            editor: this.CLASSIC_EDITOR,
            isLoading: false,
            isForServiceRequest: false,
            autoCreateService: false,
            isManageCategory: false,
            CountryDDl: '',
            StateDDl: '',
            Latitude: '',
            Longitude: '',
            saveTicketStatus: '',
            jsonData: {},
            fileMulitpleData: [],
            ticketType: '',
            isMapFeature: '',
            clientId: '',
            TicketNumber: '',
            isShowMap: true,
            isSuccessMessage: false,
            bindFrequency: [{ value: 'Daily', name: 'Daily' }, { value: 'Weekly', name: 'Weekly' }, { value: 'BiWeekly', name: 'BiWeekly' }, { value: 'Monthly', name: 'Monthly' },
            { value: 'Quarterly', name: 'Quarterly' }, { value: 'halfYearly', name: 'halfYearly' }, { value: 'Annually', name: 'Annually' }],

            bindWeekDay: [{ value: 1, name: 'Monday' }, { value: 2, name: 'Tuesday' }, { value: 3, name: 'Wednesday' }, { value: 4, name: 'Thursday' }, { value: 5, name: 'Friday' },
            { value: 6, name: 'Saturday' }, { value: 7, name: 'Sunday' }],
            StaticObj: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ClientAddress'),
                            name: "AddrTo",
                            value: "",
                            disabled: false,
                            config: {
                                options: [],
                                onChange: this.onChangeDropdown

                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",

                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Address'),
                            name: "Address",
                            value: "",
                            placeholder: this.$t('PleaseEnterAddress'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "country_id",
                            value: "",
                            disabled: true,
                            config: {
                                options: [],
                                onChange: this.GetStateDDL
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            disabled: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('County'),
                            name: "County",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterCounty'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterCityName'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Street'),
                            name: "Street",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterStreet'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "Zip",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterZipCode'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('NoteAboutAddress'),
                            name: "Notes",
                            value: "",
                            disabled: false,
                            placeholder: this.$t('PleaseEnterDescription'),
                            validationRules: "",
                        },
                    ]
                }],
            FormSchema: [],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning save",
                    text: "<i class='fa fa-save pr-2'></i> Save As Draft",
                    value: "DRAFT",
                    onClick: this.saveForm
                },
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i> Submit",
                    onClick: this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: function () {
        debugger;
        var vm = this;
        if (!vm.isBlank(vm.$route.params.id) && vm.$route.params.id != "0") {
            let ticket_id = decodeURIComponent(vm.$route.params.id);
            vm.getForm(ticket_id);
        }
        else {
            if (vm.$route.params.id == "0") {
                vm.getForm(0);

            }
            else {
                vm.$router.push({ name: "add", params: { id: 0 } });
                vm.getForm(0);
            }
        }
        vm.GetCountryDDL();
    },
    methods: {
        GetAddress: function (data) {
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            debugger;
            var indexValue = data.length - 1;
            let filteredCountry = vueObj.CountryDDl.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });
            vueObj.StaticObj[1].Data[0].value = data[indexValue].CompleteAddress.FullAddress;
            var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
            vueObj.StaticObj[1].Data[1].value = filterCountry;
            vueObj.GetStateDDL(null, filterCountry);
            setTimeout(function () {
                let filteredState = vueObj.StateDDl.filter((item) => {
                    return item.name === data[indexValue].CompleteAddress.AdministrativeAreaLevel1.LongName;
                });
                var filterState = filteredState == '' ? '' : filteredState[0].value;
                vueObj.StaticObj[1].Data[2].value = filterState;
            }, 1000);
            vueObj.StaticObj[1].Data[3].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel2.LongName;
            vueObj.StaticObj[1].Data[4].value = data[indexValue].CompleteAddress.City.LongName;
            vueObj.StaticObj[1].Data[5].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel3.LongName;
            vueObj.StaticObj[1].Data[6].value = data[indexValue].CompleteAddress.PinCode;
            var dataLat1 = ''; var dataLat2 = ''; var dataLng1 = ''; var dataLng2 = '';
            data[indexValue].LatLng.forEach((item, index) => {
                if (index == 0) {
                    dataLat1 = item.Lat;
                    dataLat2 = item.Lng;
                }
                else {
                    dataLng1 = item.Lat;
                    dataLng2 = item.Lng;
                }
            });
            vueObj.Latitude = dataLng1 + ', ' + dataLat2;
            vueObj.Longitude = dataLat1 + ', ' + dataLng2;


            vueObj.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    if (group.group_name == "GeneralInformation") {
                        if (field.name == "location_se") {
                            field.value = vueObj.Latitude;
                        }
                        if (field.name == "location_nw") {
                            field.value = vueObj.Longitude;
                        }
                    }
                });
            });
        },

        Close() {
            this.$router.push({ name: "TicketingList" });
        },

        GetCountryDDL: async function (event) {
            var vm = this;
            vm.StaticObj[1].Data[1].config.options = [];
            vm.CountryDDl = '';
            var appendingUrl = `id=&&moduleName=TICKETING&type=${'COUNTRIES'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.CountryDDl = response.data.result.DATA;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.StaticObj[1].Data[1].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },

        GetStateDDL: async function (event, id) {
            var vm = this;
            var CountryId = 0;
            if (event == null) {
                CountryId = id;
            } else {
                CountryId = event.target.value.toString();
            }
            vm.StaticObj[1].Data[2].config.options = [];
            vm.StateDDl = '';
            var appendingUrl = `id=&&moduleName=TICKETING&type=${'STATE'}&search=&refId=` + CountryId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.StateDDl = response.data.result.DATA;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.StaticObj[1].Data[2].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },

        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },

        onChangeEventForFields: function (x, y, z) {
            debugger;
            let ths = this;
            var vm = this;
            if (y.name == "ticket_for") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "on_behalf") {
                            field.visibility = (y.value == "2");
                            field.value = "3";
                            field.label = "On Behalf";
                        }
                        if (field.name == "on_behalf_consumer_id" && y.value != "1") {
                            field.visibility = true;
                            vm.GetDropDownData('FSM_USERS', field, null);
                            field.label = 'User';
                        }
                        if (field.name == "on_behalf_consumer_id" && y.value == "1") {
                            field.visibility = false;
                        }
                        if (field.name == "ticket_channel") {
                            field.visibility = false;

                        }
                    });
                });
            }
            else if (y.name == "on_behalf") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "on_behalf_consumer_id" && y.value == "4") {
                            field.visibility = true;
                            field.label = 'Client';
                            vm.GetDropDownData('CLIENTS', field, null);
                        }
                        if (field.name == "on_behalf_consumer_id" && y.value == "3") {
                            field.visibility = true;
                            vm.GetDropDownData('FSM_USERS', field, null);
                            field.label = 'User';
                        }
                        if (field.name == "ticket_channel" && y.value == "4") {
                            field.visibility = true;
                            field.label = 'Channel';
                        }
                        if (field.name == "ticket_channel" && y.value == "3") {
                            field.visibility = false;
                            field.label = 'Channel';
                        }
                    });
                });
            }
            else if (y.name == "ticket_category_id") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        debugger;
                        let checkIsForServiceRequest = "";
                        // if (z == "EDIT") {
                        //     checkIsForServiceRequest = y.config.options.find(f => f.value == x)?.first_value;
                        //     vm.isForServiceRequest = "";
                        //     vm.isForServiceRequest = checkIsForServiceRequest == "1" ? true : false;
                        // }
                        if (field.name == y.name) {
                            // let checkIsForServiceRequest = field.config.options.find(x => x.value == y.value)?.first_value;
                            if (!vm.isBlank(field.config.options.find(f => f.value == y.value)?.first_value)) {
                                checkIsForServiceRequest = field.config.options.find(f => f.value == y.value)?.first_value;
                            }
                            vm.isForServiceRequest = "";
                            vm.isForServiceRequest = checkIsForServiceRequest == "1" ? true : false;
                        }
                        if (vm.isForServiceRequest == true) {
                            if (field.name == "ticket_for") {
                                field.visibility = false;
                                field.value = 1;
                            }
                            if (field.name == "on_behalf") {
                                field.visibility = false;
                                field.value = null;
                            }
                            if (field.name == "on_behalf_consumer_id") {
                                field.visibility = true;
                                field.label = 'Client';
                                vm.GetDropDownData('CLIENTS', field, null);
                            }
                            if (field.name == "ticket_channel") {
                                field.visibility = true;
                                field.label = 'Channel';
                            }
                            if (group.group_name == "Preferred Time" || group.group_name == "Address") {
                                group.visibility = true;
                                if (field.name == "START_DATE" || field.name == "END_DATE" || field.name == "addresFields") {
                                    field.visibility = true;
                                    field.config = { mode: 'DateTime', format: "M/D/YYYY" }
                                }
                            }
                            if (group.group_name == "Recurring") {
                                group.visibility = true;
                                if (field.name == "frequency") {
                                    field.visibility = true;
                                    field.config.options = vm.bindFrequency;
                                }
                                if (field.name == "recurrence") {
                                    field.visibility = true;
                                }
                            }
                            if (field.name == "is_field_service") {
                                field.value = "";
                                field.visibility = false;
                                field.value = 1;
                            }
                        }
                        else if (vm.isForServiceRequest == false) {
                            if (field.name == "ticket_for") {
                                field.visibility = true;
                                field.value = 1;
                            }
                            if (field.name == "on_behalf_consumer_id" || field.name == "cdc_id" || field.name == "work_type_id" || field.name == "utility_id" || field.name == "ticket_channel") {
                                field.visibility = false;
                            }
                            if (group.group_name == "Scheduled Appointment" || group.group_name == "Preferred Time" || group.group_name == "Address") {
                                group.visibility = false;
                                if (field.name == "ScheduledStartDate" || field.name == "ScheduledEndDate" || field.name == "crew_id" || field.name == "resource_id" || field.name == "START_DATE" || field.name == "END_DATE" || field.name == "addresFields") {
                                    field.visibility = false;
                                }
                            }
                            if (group.group_name == "Recurring") {
                                group.visibility = false;
                                if (field.name == "frequency") {
                                    field.visibility = false;
                                    field.config.options = vm.bindFrequency;
                                }
                                if (field.name == "recurrence" || field.name == "weekly" || field.name == "biweekly" || field.name == "recurringDate") {
                                    field.visibility = false;
                                }
                            }

                            if (field.name == "is_field_service") {
                                field.value = "";
                                field.visibility = false;
                                field.value = 0;
                            }
                        }
                    });
                });
            }
            else if (y.name == "frequency") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (group.group_name == "Recurring") {
                            if (y.value == "Daily") {
                                if (field.name == "biweekly" || field.name == "recurringDate" || field.name == "weekly") {
                                    field.visibility = false;
                                }
                            }
                            if (y.value == "Weekly") {
                                if (field.name == "weekly") {
                                    field.visibility = true;
                                    field.config.options = vm.bindWeekDay;
                                }
                                if (field.name == "biweekly" || field.name == "recurringDate") {
                                    field.visibility = false;
                                }
                            }
                            if (y.value == "BiWeekly") {
                                if (field.name == "biweekly") {
                                    field.visibility = true;
                                    field.config.options = vm.bindWeekDay;
                                }
                                if (field.name == "weekly" || field.name == "recurringDate") {
                                    field.visibility = false;
                                }
                            }
                            if (y.value == "Monthly" || y.value == "Quarterly" || y.value == "halfYearly" || y.value == "Annually") {
                                if (field.name == "recurringDate") {
                                    field.visibility = true;
                                }
                                if (field.name == "weekly" || field.name == "biweekly") {
                                    field.visibility = false;
                                }
                            }
                        }
                    });
                });
            }
            else if (y.name == "on_behalf_consumer_id") {
                debugger;
                vm.GetClientaddress(null, z.value);

                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        // debugger;
                        if (field.name == "utility_id" && vm.isForServiceRequest == true && vm.isMapFeature == 'locating') {
                            let id = z.value;
                            field.visibility = true;
                            field.label = 'Utility Name';
                            vm.GetDropDownData('UTILITY_LIST_BY_CLIENT_ID', field, id);
                        }
                        else if (field.name == "work_type_id" && vm.isMapFeature == 'nonlocating') {
                            let id = y.value;
                            field.visibility = true;
                            vm.GetDropDownData('FSM_WORKTYPE', field, null);
                        }
                    });
                });
            }
            else if (y.name == "utility_id") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "cdc_id") {
                            let id = y.value;
                            field.visibility = true;
                            vm.GetDropDownData('FSM_CDCCODE_FOR_TICKET', field, id);
                        }
                    });
                });
            }

            else if (y.name == "cdc_id") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (field.name == "work_type_id") {
                            let id = y.value;
                            field.visibility = true;
                            vm.GetDropDownData('FSM_WORKTYPE_BY_CDC', field, id);
                        }
                    });
                });
            }
            else if (y.name == "work_type_id") {
                ths.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        let checkIsAutoCreateService = "";
                        if (!vm.isBlank(y.config.options.find(x => x.value == z.value)?.first_value)) {
                            checkIsAutoCreateService = y.config.options.find(x => x.value == z.value)?.first_value;
                        }
                        vm.autoCreateService = checkIsAutoCreateService == "1" ? true : false;
                        if (vm.autoCreateService == true) {
                            if (group.group_name == "Scheduled Appointment") {
                                group.visibility = true;

                                if (field.name == "ScheduledStartDate" || field.name == "ScheduledEndDate" || field.name == "crew_id" || field.name == "resource_id") {
                                    field.visibility = true;
                                    field.config = { mode: 'DateTime', format: "M/D/YYYY" }
                                }
                            }
                            if (field.name == "crew_id") {
                                vm.GetDropDownData('FSM_CREWNAME', field, null);
                            }
                            if (field.name == "resource_id") {
                                vm.GetDropDownData('FSM_SA_RESOURCENAME', field, null);
                            }
                        }
                        else if (vm.autoCreateService == false) {
                            if (group.group_name == "Scheduled Appointment") {
                                group.visibility = false;

                                if (field.name == "ScheduledStartDate" || field.name == "ScheduledEndDate" || field.name == "crew_id" || field.name == "resource_id") {
                                    field.visibility = false;
                                }
                            }
                        }
                    });
                });
            }
            else if (y.name == "attachment") {
                // debugger;
                ths.GetBase64String(y.value);
            }
        },
        GetClientaddress: async function (event, id) {
            var vm = this;
            var clientId = 0;
            if (event == null) {
                clientId = id;
            } else {
                clientId = event.target.value.toString();
                vm.clientId = clientId;
            }
            vm.StaticObj[0].Data[0].config.options = [];
            var appendingUrl = `id=&&moduleName=TICKETING&type=${'CLIENT_ADDRESS_LIST_WO'}&search=&refId=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.StaticObj[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                            if (item.first_value == "1") {
                                vm.StaticObj[0].Data[0].value = item.value;
                                vm.BindAddressUsingClientAdrress(clientId, item.value);
                            }
                        })
                    }
                }
            });
            //vm.GetUtilityDDL(event, id);
        },
        onChangeDropdown: async function (e) {
            var vm = this;
            var dropName = e.target.name;
            var clientAddressId = '';
            if (dropName == "AddrTo") {
                clientAddressId = e.target.value;
            }
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    if (field.name == "on_behalf_consumer_id") {
                        let clientid = field.value.value;
                        if (!vm.isBlank(clientid) && clientAddressId != '') {
                            vm.BindAddressUsingClientAdrress(clientid, clientAddressId);
                        }
                    }
                });
            });
        },
        BindAddressUsingClientAdrress: function (clientid, clientAddressId) {
            var vueObj = this;

            var url = `id=${clientid}&moduleName=TICKETING&addressId=${clientAddressId}`;
            DataServices.GetTicketClientAddress(url).then(response => {

                if (response.data.result != null && response.data.result.data.length > 0) {
                    vueObj.StaticObj[1].Data[0].value = response.data.result.data[0].USER_ADDRESS_1;
                    vueObj.StaticObj[1].Data[1].value = response.data.result.data[0].COUNTRY_ID;
                    vueObj.StaticObj[1].Data[2].value = response.data.result.data[0].CT[0].ST[0].STATE_ID;
                    vueObj.StaticObj[1].Data[4].value = response.data.result.data[0].USER_CITY;
                    vueObj.StaticObj[1].Data[6].value = response.data.result.data[0].USER_ZIPCODE;
                    vueObj.StaticObj[1].Data[7].value = response.data.result.data[0].USER_ADDRESS_DESC;
                }
            })
        },
        GetDropDownData: function (type, field, id) {

            var vm = this;
            if (!vm.isBlank(id)) {
                var url = `id=${id}&moduleName=TICKETING&type=${type}&search=&refId=`;
            }
            else {
                var url = `id=&moduleName=TICKETING&type=${type}&search=&refId=`;
            }
            DataServices.GetDDLData(url).then(response => {
                field.config.options = [];
                field.value = '';
                if (response.status == 200) {
                    if (type == "FSM_USERS" || type == "CLIENTS" || type == "FSM_CREWNAME" || type == "FSM_SA_RESOURCENAME" || type == "UTILITY_LIST_BY_CLIENT_ID" || type == "FSM_CDCCODE_FOR_TICKET" || type == "TICKET_CATEGORY") {
                        response.data.DATA.forEach((item) => {
                            field.config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    if (type == "FSM_WORKTYPE_BY_CDC" || type == "FSM_WORKTYPE") {
                        response.data.DATA.forEach((item) => {
                            field.config.options.push({
                                value: item.value,
                                name: item.name,
                                first_value: item.first_value,
                                second_value: item.second_value
                            })
                        })
                    }

                }
            })
        },
        getForm: function (id) {
            var ths = this;
            ths.isLoading = true;

            var url = `recordId=${id}&moduleName=Ticketing&subModuleCode=Ticketing`;
            DataServices.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {
                    debugger;
                    ths.isMapFeature = response.data.isFsmLocating;
                    ths.FormSchema = ths.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    ths.FormSchema.forEach((group, gi) => {

                        group.Data.forEach((field, fi) => {
                            if (group.group_name == "Scheduled Appointment" || group.group_name == "Preferred Time" || group.group_name == "Recurring") {
                                group.visibility = false;
                                if (field.name == "ScheduledStartDate" || field.name == "ScheduledEndDate" || field.name == "crew_id" || field.name == "resource_id" || field.name == "START_DATE" || field.name == "END_DATE" || field.name == "frequency" || field.name == "weekly" || field.name == "biweekly" || field.name == "recurringDate") {
                                    field.visibility = false;
                                }
                                if (field.name == "recurrence") {
                                    field.visibility = false;
                                    field.astype = 'NumericField';
                                }
                            }
                            if (field.name == "is_read") {
                                field.value = 0;
                            }
                            if (field.name == "cc_users") {
                                field.mode = "tag";
                            }
                            if (field.name == "ticket_for") {
                                field.value = "1";
                                field.label = "Ticket For";
                            }
                            if (field.name == "ticket_category_id") {
                                // debugger;
                                // if (!ths.isBlank(field.value)) {
                                //     ths.onChangeEventForFields(field.value, field, 'EDIT');
                                // }
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = ths.OpenCategoryModal;
                            }
                            if (field.name == "on_behalf_consumer_id" || field.name == "location_nw" || field.name == "location_se" || field.name == "TicketType" || field.name == "is_field_service" || field.name == "on_behalf" || field.name == "ticket_channel" || field.name == "utility_id" || field.name == "cdc_id" || field.name == "work_type_id") {
                                field.visibility = false;
                            }
                            if (field.name == "Description") {
                                field.showLabel = false;
                            }
                            if (field.name == "attachment") {
                                field.showLabel = false;
                                field.config =
                                {
                                    maxSize: 5,
                                    accept: [
                                        {
                                            fileType: 'doc', // Document type
                                            iconClass: 'fa-file-word-o' // Icon class for documents
                                        },
                                        {
                                            fileType: 'docx', // Document type
                                            iconClass: 'fa-file-word-o' // Icon class for documents
                                        },
                                        {
                                            fileType: 'txt', // Text file type
                                            iconClass: 'fa-file-text-o' // Icon class for text files
                                        },
                                        {
                                            fileType: 'jpg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'jpeg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'png', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'gif', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'bmp', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'mp4', // Video types
                                            iconClass: 'fa-file-video-o' // Icon class for videos
                                        },
                                        {
                                            fileType: 'mov', // Video types
                                            iconClass: 'fa-file-video-o' // Icon class for videos
                                        },
                                        // Add more image, document, text, and video types as needed
                                    ],
                                    //onChange: ths.GetBase64String,
                                    showAddIcon: false,
                                    onAddButtonClick: '',
                                    multiple: true
                                }
                            }
                            field.config.onChange = ths.onChangeEventForFields;
                        });
                    });
                    ths.FormSchema.push({
                        layoutType: "single",
                        group_name: 'Address',
                        visibility: false,
                        Data: [{
                            astype: "SlotField",
                            name: "addresFields",
                            value: "",
                            visibility: false,
                            showLabel: false
                        }]
                    });

                    ths.isLoading = false;
                }
            });
        },
        OpenCategoryModal: function () {
            var vm = this;
            vm.isManageCategory = true;
        },

        GetBase64String: function (evt) {
            try {
                var vm = this;
                var f = evt[0]; // FileList object
                // var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                let fileObj = {};
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        fileObj.attachement_mime = attachemnetMime,
                            fileObj.fileName = fileName,
                            fileObj.base64String = base64StringEncoded,
                            fileObj.fileSize = f.size,
                            fileObj.contentType = f.type,
                            fileObj.ModuleName = 'TICKETING',
                            fileObj.SubModuleCode = 'TICKETING'
                        vm.fileMulitpleData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);

            }
            catch (ex) {
            }
        },
        saveForm: function (btnType) {
            var ths = this;
            debugger;
            ths.$refs.AddTicket.validate().then(result => {
                if (result) {
                    if (btnType.value == "DRAFT") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (group.group_name == "GeneralInformation") {
                                    if (field.name == "TicketType") {
                                        field.value = 'DRAFT';
                                    }
                                }
                            });
                        });
                        ths.ticketType = 'DRAFT';
                    }
                    ths.isLoading = true;
                    var CustomFieldArray = ths.ConvertDynamicFormObjToCustomFieldObj(ths.FormSchema);
                    var CustomFieldArrayStatic = ths.ConvertDynamicFormObjToCustomFieldObj(ths.StaticObj);

                    CustomFieldArray.forEach((x) => {
                        if (x.field_name == "on_behalf_consumer_id") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                        if (x.field_name == "work_type_id") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                        if (x.field_name == "cc_users") {
                            if (!ths.isBlank(x.field_value)) {
                                var CCUsers = '';
                                var i = 0;
                                for (i = 0; i < x.field_value.length; i++) {
                                    if (i == 0) {
                                        CCUsers = x.field_value[i].value;
                                    } else {
                                        CCUsers = CCUsers + "," + x.field_value[i].value;
                                    }
                                }
                                x.field_value = CCUsers;
                            }
                        }
                        if (x.field_name == "weekly") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                    })
                    CustomFieldArrayStatic.forEach((x) => {
                        CustomFieldArray.push(x);
                    });

                    var obj = {
                        data: CustomFieldArray,
                        moduleCode: "Ticketing",
                        subModuleCode: "Ticketing",
                        attachmentData: ths.fileMulitpleData.length > 0 ? ths.fileMulitpleData : null
                    }
                    DataServices.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                        debugger;
                        ths.isLoading = false;
                        if (response != null && response.data != null && response.data[0].Status == "Success") {
                            ths.TicketNumber = JSON.parse(response.data[0].Value.OTHER_DATA).TICKET_NUMBER;
                            ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("Ticket")]), "success", true, ths.$t("Alert"));
                            ths.isSuccessMessage = true;
                            if (ths.ticketType == 'DRAFT') {
                                ths.saveTicketStatus = ths.$t("TicketDraftSubmitStatus");
                            }
                            else {
                                ths.saveTicketStatus = ths.$t("TicketSubmitStatus");
                            }
                        } else {
                            ths.ShowAlert(ths.$t("ErrorWhileSaving", [ths.$t("Ticket")]), "danger", true, ths.$t("Alert"));
                            ths.isSuccessMessage = false;
                        }
                    })
                }
            })
        }

    }
}
</script>